html,
body {
  position: relative;
  width: 100%;
  height: 100%; }

body {
  font-size: 14px; }

[v-cloak] {
  display: none; }

.loading {
  display: block;
  position: absolute;
  z-index: 10; }
  .loading:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    z-index: 9; }
  .loading:after {
    content: '';
    position: absolute;
    top: calc(50% - 3.5px);
    left: calc(50% - 3.5px);
    width: 31px;
    height: 31px;
    margin: 0 auto;
    border: 2px solid rgba(97, 100, 193, 0.98);
    border-radius: 50%;
    border-left-color: transparent;
    border-right-color: transparent;
    animation: cssload 425ms infinite linear; }

@keyframes cssload {
  100% {
    transform: rotate(360deg); } }

.bodyLoad {
  width: 100%;
  height: 100%; }

.bodyWrap {
  position: relative;
  width: 100%;
  height: 100%;
  background: #f0f0f0; }

.menu {
  margin: 0 auto; }
